<template>
  <div class="movie">
    <v-logo></v-logo>
    <p class="in-theaters">正在热映</p>
    <ul>
      <li v-for="(item,index) in movieList" class="movie-item">
        <span>{{index+1+'.'}}</span>
        <img :src="item.images.small" :alt="item.title" class="movie-img">
        <span>{{item.title | movieTitle}}</span>
        <span>{{item.rating.average | toFixed1}}</span>
      </li>
    </ul>
  </div>
</template>
<script>
  import logo from './logo.vue';
  /*data是函数！真正的值需要放在return的返回值中*/
  export default{
    data(){
      return {
        movieList: []
      }
    },
    filters: {
      movieTitle: function (val) {
        if (val.length >= 4) {
          val = val.substring(0, 4) + '...';
        }
        return val;
      },
      toFixed1:function (val) {
        return val.toFixed(1);
      }
    },
    mounted: function () {
      this.$http.jsonp('https://api.douban.com/v2/movie/in_theaters'+ "?start=0&count=10").then((response) => {
        console.log(response.body.subjects)
        this.movieList = response.body.subjects;
      }, (response) => {
        console.log('err')
      });

    },
    components: {
      'v-logo': logo
    }
  }
</script>

<style>
  .movie {display: flex;flex-direction: column;}
  .in-theaters{font: 24px/32px "微软雅黑";padding-left: 20px;}
  .movie-item{display: flex;justify-content: space-between;padding: 10px 20px;font: 16px/24px "微软雅黑";}
  .movie-img{height: 100px;width: 64px;}
</style>


