    <img src="../assets/4.png" alt="logo" class="logo-img">
  </div>
</template>
<script>
  export default{}
</script>

<style>
  .logo-container{display: flex;justify-content:center;padding: 10px 0 ;}
  .logo-img {
    width: 50px;
    height: 50px;
  }

  @keyframes myanimation {
    from {
    }
