<template>
  <div class="hello">
    <h3>{{ msg }}</h3>
    <v-movie></v-movie>
  </div>
</template>
<script>
  import movie from './movie.vue'
  export default {
    data () {
      return {
        msg: 'Welcome to Your Vue.js App'
      }
    },
    components:{
      'v-movie':movie
    }
  }
</script>
<style>
  .hello{width: 90%;margin: 0 auto;}
  h3{text-align: center;}
</style>



